* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}


.container-fluid {
  width: 90%;
  margin: auto;
}

.table-responsive {
  max-height: 700px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
}

tr:hover {
  /* background-color: rgb(219, 219, 142); */
  background-color:#e5eaf5;
}

td,
th {
  position: relative;
}

.table td,
.table th {
  padding: 0.75rem;
  vertical-align: top;
  border-top: none;
}

td:hover::after,
th:hover::after {
  content: "";
  position: absolute;
  background-color: #f2f2f2;
  left: 0;
  top: -5000px;
  height: 5000px;
  width: 100%;
  z-index: -1;
}

.heading-span{
  color: rgb(255, 0, 55);
  font-weight:bolder;
}

.table tr td:hover:nth-child(4) {
  background-color: rgb(231, 83, 115);
  color: aliceblue;
  border: 0.5px solid rgb(12, 12, 12);
}

.table tr td:hover {
  background-color: rgb(117, 223, 122);
  color: aliceblue;
  border: 0.5px solid rgb(12, 12, 12);
}

.thead-dark {
  font-weight: bold;
  text-transform: uppercase;
}

.table-container{
  overflow: hidden;
}

.footer{
  text-align: center;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: black;
  font-size: 1.3rem;
  color: white;
  text-align: center;
}

.footer p span{
  color: white;
}